/*VARIABLES*/
:root {
  --primary-color: rgb(14, 24, 24);
  --secondary-color: rgb(145, 144, 140);
  --highlight-color: rgb(234, 31, 73);
}

/*RESETS*/
* {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box
}



.underline{
	width: 50%;
	border: 2px solid rgb(0, 0, 0);
	margin: auto;
}
.hero figure img{
  width: 60%;
  height: auto;
 
}

.name{
  color: var(--secondary-color) !important;
}

.name a {
  color: var(--secondary-color) !important;
}

.name a:hover {
  color: var(--highlight-color) !important;
 
}

.h1 {
  color: var(--highlight-color) !important;
 
}

.navbar{
  background-color: var(--primary-color) !important;
  margin-bottom: 30px;
}

.nav-link {
  color: var(--secondary-color) !important;
  text-decoration: none !important;
  
}

.nav-link:hover {
  color: var(--highlight-color) !important;
 
}



form {
 width: 400px;
 display:flex;
 flex-direction: column;
 margin: 0 auto;

}

form > * {

padding: 10px;
margin-bottom: 25px;
border: 1px solid var(--secondary-color) !important;
outline: none;
}

button{

  background-color: var(--primary-color) !important;
  border: 1px solid var(--primary-color) !important;
  color: var(--highlight-color) !important;
  font-size: 1.5rem;
  margin-bottom: 20px;
}


.footer {
  background-color: var(--primary-color) !important;
}

.card {
width: 100%;
margin-bottom: 3rem;

}


.card a {
 
  color: var(--highlight-color) !important;
  text-decoration: none !important;
  font-size: 20px;
  
}

.card a:hover {
  color: var(--secondary-color) !important;
  
  font-size: 20px;
 
}



.contact a {
 
  color: var(--highlight-color) !important;
  text-decoration: none !important;
  font-size: 20px;
  margin-bottom: 50px;
  
}

.contact a:hover {
  color: var(--secondary-color) !important;
  font-size: 20px;
 
}



/* MOBILE STYLE */

@media screen and (max-width: 600px) {

  .contact {
    display: flex;
    flex-direction: column;
  }

}